<template>
	<view class="body">
		<view v-html="data"></view>
	</view>
</template>

<script>
	import { agreement } from '@/common/http.api.js';
	export default {
		data() {
			return {
				show:true,
				data:''
			}
		},
		onLoad(option) {
			let type = ''
			// 1 用户协议/2 隐私政策
			if(option.status==1){
				this.show = true
				type = 'user_agreement'
				uni.setNavigationBarTitle({
					title: "用户协议"
				});
			}else{
				this.show = false
				type = 'privacy_agreement'
				uni.setNavigationBarTitle({
					title: "用户隐私权保护政策"
				});
			}
			agreement({type}).then(res=>{
				console.log(res)
				this.data = res.data.data.content
			}).catch(err=>{
				console.log(err)
			})
		},
		methods:{
			
		}
	}
</script>

<style lang="scss">
	.body{
		margin: 0 20rpx;
	}
</style>